@import url(https://fonts.googleapis.com/css2?family=Inter:wght@700&family=Manrope:wght@400;700&family=Roboto:wght@400;500;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.contain{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}.header{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.header__title{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center;width:300px;height:40px;border:solid 1px #d8d6d6;border-radius:5px;font-weight:700;font-size:24px;margin:20px 0}.content{display:grid;grid-template-columns:1fr 1fr;padding:50px 20px 72px}@media (max-width: 767px){.content{padding:30px 10px 10px}}.content__container{display:grid;-webkit-justify-content:space-around;justify-content:space-around}@media (min-width: 1200px){.content__container{grid-column:span 3;grid-row-gap:46px;row-gap:46px;grid-column-gap:48px;-webkit-column-gap:48px;column-gap:48px}}@media (max-width: 1199px){.content__container{grid-column:span 3;grid-row-gap:26px;row-gap:26px;grid-column-gap:18px;-webkit-column-gap:18px;column-gap:18px}}@media (max-width: 767px){.content__container{grid-column:span 1;grid-column-gap:18px;-webkit-column-gap:18px;column-gap:18px}}.content__button{font-weight:700;font-size:20px;grid-column:span 3;background-color:#045dc4;color:#fff;border-radius:5px;height:40px;cursor:pointer;border:1px solid #0465d4;transition:background-color 0.3s, color 0.3s, box-shadow 0.3s}@media (max-width: 767px){.content__button{grid-column:span 1}}.content__button:hover{box-shadow:0px 3px 13px 0px #c5c5c5;background-color:#fff;color:#045dc4;border:1px solid #fff}.content__contain{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100vh;position:-webkit-sticky;position:sticky;z-index:1;top:0;-webkit-transform:translateY(0);transform:translateY(0)}

*{box-sizing:border-box;font-family:'Manrope', sans-serif}h1,p,button{margin:0;padding:0}

.base-container{width:300px;position:relative;margin-left:20px}.base-select{font-size:14px;font-weight:700;width:100%;height:40px;border-radius:5px;border:1px solid #d8d6d6;background-color:#fff;cursor:pointer;outline:none;padding:0 12px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;transition:border 0.3s, outline 0.3s;position:relative}.base-select:hover{border:1px solid #0465d4}.base-select.base-select-open{outline:1px solid #f8f085}.options{background-color:#fff;border:1px solid #d8d6d6;border-radius:5px;width:100%;padding-left:0;max-height:200px;overflow-y:auto;list-style-type:none;margin-top:4px;box-shadow:0px 2px 15px 0px #c5c5c5;position:absolute;z-index:1;scroll-behavior:smooth}.list{font-weight:700;font-size:14px;color:#252525;padding:8px 10px;border-radius:5px;cursor:pointer;display:block;transition:color 0.3s, background-color 0.3s}.list:hover{background-color:#f1f1f1;border-radius:0}.icon{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

.card{border:1px solid #d8d6d6;border-radius:5px;background-color:#fff;padding:32px 16px 16px;cursor:pointer}@media (min-width: 1200px){.card{width:200px}}@media (max-width: 1199px){.card{width:160px}}.card__photo{display:block;height:134px;margin:0 auto 40px;object-fit:cover}@media (min-width: 1200px){.card__photo{width:160px}}@media (max-width: 1199px){.card__photo{width:120px}}.card__header{margin:0 0 4px;font-size:12px;font-weight:700;line-height:18px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.card__block{display:grid;grid-gap:0 10px;gap:0 10px;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin:0 0 16px}@media (min-width: 1200px){.card__block{grid-template-columns:repeat(2, 70px)}}@media (max-width: 1199px){.card__block{grid-template-columns:repeat(2, 50px)}}.card__title{width:100%;height:25px;background-color:#d7fae5;border-radius:5px;color:#1a1a1a;font-size:12px;font-weight:700}.card__title_type{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:100%;height:100%}

.container{border:1px solid #d8d6d6;border-radius:5px;background-color:#fff;padding:32px 16px 16px}@media (min-width: 1200px){.container{width:300px}}@media (max-width: 1199px){.container{width:200px}}@media (max-width: 767px){.container{width:170px;padding:32px 10px 16px}}.container__photo{display:block;height:134px;margin:0 auto 40px;object-fit:cover}@media (min-width: 1200px){.container__photo{width:160px}}@media (max-width: 1199px){.container__photo{width:120px}}@media (max-width: 767px){.container__photo{width:100px;margin:0 auto 20px}}.container__header{margin:0 0 20px;font-size:24px;font-weight:700;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.container__block{display:grid;grid-template-columns:3fr 1fr;border:1px solid #d8d6d6;border-radius:5px}.container__title{font-weight:400;font-size:14px;border:1px solid #d8d6d6;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}

